<template>
  <div id="" class="tablewin">
    <div>
      <div
        :style="{'background':'#f2f2f2','height':'40px','padding-top':'10px','padding-bottom':'5px'}"
      >
        <h3 :style="{'color':'black'}"></h3>
      </div>
      <div :style="{'background':'white','padding-top':'20px','padding-bottom':'20px'}">
        <label :style="{'display':'inline'}">
          <div :style="{'display':'inline'}"></div>
          <el-input
            v-model="idKey"
            clearable
            :style="{'width':'20%'}"
            @change="search(idKey,nameKey,partAKey,partBKey,strtimeKey,orderStateKey)"
          ></el-input>
        </label>
        <label :style="{'display':'inline'}">
          <div :style="{'display':'inline'}"></div>
          <el-input
            v-model="nameKey"
            clearable
            :style="{'width':'20%'}"
            @change="search(idKey,nameKey,partAKey,partBKey,strtimeKey,orderStateKey)"
          ></el-input>
        </label>
        <label :style="{'display':'inline'}">
          <div :style="{'display':'inline'}"></div>
          <el-input
            v-model="orderStateKey"
            clearable
            :style="{'width':'20%'}"
            @change="search(idKey,nameKey,partAKey,partBKey,strtimeKey,orderStateKey)"
          ></el-input>
        </label>
        <p style="margin-top:20px"></p>
        <label>
          <div :style="{'display':'inline'}"></div>
          <el-input
            v-model="partAKey"
            clearable
            :style="{'width':'20%'}"
            @change="search(idKey,nameKey,partAKey,partBKey,strtimeKey,orderStateKey)"
          ></el-input>
        </label>
        <label :style="{'display':'inline'}">
          <div :style="{'display':'inline'}"></div>
          <el-input
            v-model="partBKey"
            clearable
            :style="{'width':'20%'}"
            @change="search(idKey,nameKey,partAKey,partBKey,strtimeKey,orderStateKey)"
          ></el-input>
        </label>
        <label :style="{'display':'inline'}">
          <div :style="{'display':'inline'}"></div>
          <el-input
            v-model="strtimeKey"
            clearable
            :style="{'width':'20%'}"
            @change="search(idKey,nameKey,partAKey,partBKey,strtimeKey,orderStateKey)"
          ></el-input>
        </label>
      </div>
    </div>
    <el-table :data="tableData" border height="520" :style="{'width':'100%'}"  @cell-click="detail">
      <el-table-column prop="id" label="" width="100" align="center"></el-table-column>
      <el-table-column prop="company" label="" width="160" align="center"></el-table-column>
      <el-table-column prop="sender" label="" width="160" align="center"></el-table-column>
      <el-table-column prop="receiver" label="" width="160" align="center"></el-table-column>
      <el-table-column prop="time" label="" width="160" align="center"></el-table-column>
      <el-table-column prop="orderState" label="" align="center"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      list: [
        {
          id: "1",
          name: "",
          partA: "",
          partB: "",
          ctime: new Date(),
          strtime: "",
          orderState: "",//
          goods: "apple*2,banana*3", //
          orderTracking: "", //
          tokenCost:"", //
        },
        {
          id: "2",
          name: "",
          partA: "",
          partB: "",
          ctime: new Date(),
          strtime: "",
          orderState: ""
        },
        {
          id: "3",
          name: "",
          partA: "",
          partB: "",
          ctime: new Date(),
          strtime: "",
          orderState: ""
        },
        {
          id: "4",
          name: "",
          partA: "",
          partB: "",
          ctime: new Date(),
          strtime: "",
          orderState: ""
        },
        {
          id: "5",
          name: "",
          partA: "",
          partB: "",
          ctime: new Date(),
          strtime: "",
          orderState: ""
        },
        {
          id: "6",
          name: "",
          partA: "",
          partB: "",
          ctime: new Date(),
          strtime: "",
          orderState: ""
        },
        {
          id: "7",
          name: "",
          partA: "",
          partB: "",
          ctime: new Date(),
          strtime: "",
          orderState: ""
        },
        {
          id: "8",
          name: "",
          partA: "",
          partB: "",
          ctime: new Date(),
          strtime: "",
          orderState: ""
        },
        {
          id: "9",
          name: "",
          partA: "",
          partB: "",
          ctime: new Date(),
          strtime: "",
          orderState: ""
        },
        {
          id: "10",
          name: "",
          partA: "",
          partB: "",
          ctime: new Date(),
          strtime: "",
          orderState: ""
        },
        {
          id: "11",
          name: "",
          partA: "",
          partB: "",
          ctime: new Date(),
          strtime: "",
          orderState: ""
        },
        {
          id: "12",
          name: "",
          partA: "",
          partB: "",
          ctime: new Date(),
          strtime: "",
          orderState: ""
        }
      ],
      tableData: [],
      id: "",
      name: "",
      partA: "",
      partB: "",
      ctime: "",
      orderState: "", //
      goods: "", //
      orderTracking: "", //
      tokenCost:"", //

      idKey: "",
      nameKey: "",
      partAKey: "",
      partBKey: "",
      strtimeKey: "", //
      orderStateKey: "",  //
      geturl:""
    };
  },
  created() {
    this.init()
    this.getList()
  },
  methods: {
    init(){
      switch (this.userInfo.ctype){
        case this.comType.enterprise:
          this.geturl="http://localhost:8088/trans/list"
          break;
        case this.comType.transport:
          this.geturl="http://localhost:8088/trans/list"
          break;
      }
    },
    getList(){
      this.$api({
        url: "http://localhost:8088/trans/list",
        method:"get",
        params: {
          company_name : this.userInfo.cname,
        }
      }).then(data =>{
        console.log("")
        // console.log(this.company_name);
        // console.log(data);
        this.tableData = data;
        this.list=this.tableData
      })
    },
    search(idKey, nameKey, partAKey, partBKey, strtimeKey,orderStateKey) {
      this.tableData = [];
      this.list.forEach(item => {
        if (
          item.id.toString().indexOf(idKey.toString()) != -1 &&
          item.company.indexOf(nameKey) != -1 &&
          item.sender.indexOf(partAKey) != -1 &&
          item.receiver.indexOf(partBKey) != -1 &&
          item.time.indexOf(strtimeKey) != -1 &&
          item.orderState.indexOf(orderStateKey) != -1
        )
          this.tableData.push(item);
      });
    },
    detail(item) {},
  }
};
</script>

<style>
.tablewin {
  background-color: lightblue;
}
</style>



